.nav {
  background-color: #2d2d2d;
  position: fixed;
  width: 30%;
  margin: 0 auto;
  top: 20px;
  border-radius: 50px;
  padding: 20px 0px;
  left: 50%;
  z-index: 10;
  transform: translateX(-50%);
  ul {
    display: flex;
    justify-content: center;
    gap: 50px;
    li {
      list-style: none;
      transform: none;
      transform: all 0.1s ease-in-out;
      &:hover {
        transform: scale(1.05);
      }
    }
  }
}
